export const StyledTable = styled.div`
  width: 100% !important;
  display: flex;
  flex-wrap: wrap;
  padding-left: 10px;
  @media only screen and (max-width: 804px){
    display: none;
  }
  
`;

export const StyledTableTH1 = styled.div`
    font-weight: bold;
    margin-right: 15px;
    @media only screen and (min-width: 1200px){
      margin-right: 15px;
    }
`;
export const StyledTableTH2 = styled.div`
    width: fit-content;
    flex: 0 0 27.2% ;
    font-weight: bold;
    @media only screen and (min-width: 1200px){
      flex: 0 0 29%;
    }
    @media only screen and (max-width: 993px) and (min-width: 807px){
      flex: 0 0 36%;
    }
    @media only screen and (max-width: 1200px) and (min-width: 993px){
      flex: 0 0 29.7%;
    }
`;
