export const StyledTableItemsContainer = styled.div`
  width: 100%;
  @media only screen and (max-width: 804px){
    overflow-x: scroll !important;
  }
`;


export const ProductItemsContainer = styled.div`
  padding: 10px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
`;



export const SecondContainer = styled.div`
  width: 100% !important;
  overflow-x: scroll;
  justify-content: center;
  margin-top: 10px;
  border-radius: 15px;
  box-shadow: 0px 5px 59px -9px lavender;
  @media only screen and (min-width: 1200px){
    width: 100%;
    overflow-x: visible;
  }
  @media only screen and (max-width: 804px){
    width: 750px !important;
  }
`;


interface ChipProps {
    bg?:string,
    color?:string
}

export const Chip = styled.div<ChipProps>`
  border-radius: 15px;
  background-color: ${props => props.bg || 'green'};
  width: fit-content;
  padding: 2px 15px;
  color: ${props=>props.color || 'white'};
`;



